import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { handleCheckboxClick } from './helpers/handleCheckboxClick';

import { createSections } from './sections';

// Gsap included after DOMContentLoaded
document.addEventListener('DOMContentLoaded', function () {
  // construct HTML template sections
  createSections();

  // If desktop width
  if (window.matchMedia('(min-width:960px)').matches) {
    gsap.registerPlugin(ScrollTrigger);

    ScrollTrigger.defaults({
      toggleActions: 'restart none none reverse',
      scrub: 2,
      markers: false,
      // ease: 'none',
    });

    // .section1 timeline
    let tlSection1 = gsap.timeline({});

    // Set items to be invisible on page load so they can fade in
    tlSection1.set('nav', {
      backgroundColor: 'black',
    });
    tlSection1.set('ul#menu', {
      autoAlpha: 0,
    });
    tlSection1.set('h1.section1', {
      autoAlpha: 0,
    });
    tlSection1.set('div.video', {
      autoAlpha: 0,
    });
    tlSection1.set('h1.section2', {
      autoAlpha: 0,
    });
    tlSection1.set('p.section2', {
      autoAlpha: 0,
    });

    // 1. ON PAGELOAD, stagger elements: Fade in and down
    tlSection1.fromTo(
      'nav',
      {
        backgroundColor: 'black',
      },
      {
        backgroundColor: 'white',
        duration: 1.5,
      }
    );
    // Make side-slide-nav menu visible again
    tlSection1.set('ul#menu', {
      autoAlpha: 1,
    });
    tlSection1.from(
      'h1.section1',
      {
        y: -30,
        autoAlpha: 0,
        duration: 1,
      },
      '-=1'
    );
    tlSection1.from(
      'div.video',
      {
        y: -30,
        autoAlpha: 0,
        duration: 1,
      },
      '-=.75'
    );

    // .section2: ScrollTrigger on the h1
    gsap.fromTo(
      'h1.section2',
      {
        x: -300,
        autoAlpha: 0,
      },
      {
        x: 0,
        autoAlpha: 1,
        // duration: 1,
        scrollTrigger: {
          trigger: '.section2',
          start: 'top 70%',
          end: 'center 60%',
          id: 'h1.section2',
        },
      }
    );

    // .section2: ScrollTrigger on the p
    gsap.fromTo(
      'p.section2',
      {
        x: -300,
        autoAlpha: 0,
      },
      {
        x: 0,
        autoAlpha: 1,
        scrollTrigger: {
          trigger: '.section2',
          start: 'top 50%',
          end: 'center 50%',
          id: 'p',
        },
      }
    );
    // .section2: ScrollTrigger on the img
    gsap.fromTo(
      'img.eileen1',
      {
        x: 500,
        autoAlpha: 0,
      },
      {
        x: 0,
        autoAlpha: 1,
        scrollTrigger: {
          trigger: '.section2',
          start: 'top 30%',
          end: 'center 50%',
          id: 'img.eileen1',
        },
      }
    );

    // This code applies tweens to Section3 and all HTML template sections (Section4 +):
    // Collect all selected elements and place in an array
    const h1s = gsap.utils.toArray('.section__child-left__2 > h1');

    // Loop over the array of selected elements
    h1s.forEach((item) => {
      gsap.fromTo(
        item,
        {
          y: 300,
          autoAlpha: 0,
        },
        {
          y: 0,
          autoAlpha: 1,
          scrollTrigger: {
            trigger: item,
            start: 'top 90%',
            end: 'top 60%',
            id: 'h1',
            scrub: 2,
          },
        }
      );
    });

    // collect all selected elements and place in an array
    const ps = gsap.utils.toArray('.section__child-left__2 > p');
    // loop over the array of selected elements
    ps.forEach((item) => {
      gsap.fromTo(
        item,
        {
          y: 200,
          autoAlpha: 0,
        },
        {
          y: 0,
          autoAlpha: 1,
          scrollTrigger: {
            trigger: item,
            start: 'center 160%',
            end: 'top 96%',
            id: 'p',
            scrub: 2,
            ease: 'power2',
          },
        }
      );
    });

    // collect all imgs and place in an array
    const imgs = gsap.utils.toArray('img.section__child-right');
    // loop over the array of selected elements
    imgs.forEach((item) => {
      gsap.fromTo(
        item,
        {
          x: 500,
          autoAlpha: 0,
        },
        {
          x: 0,
          autoAlpha: 1,
          scrollTrigger: {
            trigger: item,
            start: 'top 40%',
            end: '+=240',
            id: 'imgs',
            scrub: 3,
          },
        }
      );
    });

    // Pin the year number from section 3 onward
    gsap.to('.section3__child-left__1', {
      x: 0,
      autoAlpha: 1,
      color: 'white',
      scrollTrigger: {
        trigger: '.section3',
        // start: 'top 20%',
        // end: 'bottom 30%',
        end: '+=10000',
        id: 'YEAR',
        pin: '.section3__child-left__1',
        // pin: true,
      },
    });

    // Increment the year 1: 1984 to 1986
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 1984,
      },
      {
        textContent: 1986,
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-1',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-1',
          // markers: true,
        },
      }
    );
    // Increment the year 2: 1986 to 1991
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 1986,
        color: 'white',
      },
      {
        textContent: 1991,
        color: 'black',
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-2',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-2',
        },
      }
    );

    // Change h1 and p color
    gsap.fromTo(
      '.section__child-left__2',
      {
        color: 'white',
      },
      {
        color: 'black',
        scrollTrigger: {
          trigger: '.render-2',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-2',
          // markers: true,
        },
      }
    );
    // Change h1 and p color
    gsap.fromTo(
      'div.render-3 *, .year',
      {
        color: 'black',
      },
      {
        color: 'white',
        scrollTrigger: {
          trigger: '.render-3',
          start: 'top 80%',
          end: 'bottom 80%',
          id: 'render-3',
        },
      }
    );

    // Increment the year 3: 1991 to 1994
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 1991,
      },
      {
        textContent: 1994,
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-3',
          start: 'top 80%',
          end: 'bottom 80%',
          id: 'render-3',
        },
      }
    );
    // Increment the year 4: 1994 (2 SIZES) to 1994 (BALCK SHEEP)
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 1994,
      },
      {
        textContent: 1994,
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-4',
          start: 'top 80%',
          end: 'bottom 80%',
          id: 'render-4',
        },
      }
    );

    // .RENDER-4: Change h1 and p color
    gsap.fromTo(
      'div.render-4 *',
      {
        color: 'black',
      },
      {
        color: 'white',
        scrollTrigger: {
          trigger: '.render-4',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-4',
        },
      }
    );

    // .RENDER-5: Increment the year 5: 1994 to 1997
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 1994,
        color: 'white',
      },
      {
        textContent: 1997,
        color: 'black',
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-5',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-5',
        },
      }
    );

    // .render-6: Increment the year 6: 1994 to 1997
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 1997,
      },
      {
        textContent: 1998,
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-6',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-6',
        },
      }
    );

    // .render-6: Change h1 and p color
    gsap.fromTo(
      'div.render-6 *, .year',
      {
        color: 'black',
      },
      {
        color: 'white',
        scrollTrigger: {
          trigger: '.render-6',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-6',
        },
      }
    );

    // .render-7: Increment the year 7: 1994 to 1997
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 1998,
        color: 'white',
      },
      {
        textContent: 1998,
        color: 'black',
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-7',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-7',
        },
      }
    );

    // .render-8: Increment the year: 1994 to 1997
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 1998,
        color: 'black',
      },
      {
        textContent: 2000,
        color: 'white',
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-8',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-8',
        },
      }
    );

    // .render-8: Change h1 and p color
    gsap.fromTo(
      'div.render-8 *',
      {
        color: 'black',
      },
      {
        color: 'white',
        scrollTrigger: {
          trigger: '.render-8',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-8',
        },
      }
    );

    // .render-9: *INCREMENT* THE YEAR:6: 1994 to 1997
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 2000,
        color: 'white',
      },
      {
        textContent: 2002,
        color: 'black',
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-9',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-9',
          // markers: true,
        },
      }
    );
    // .render-10: *INCREMENT* THE YEAR:6: 1994 to 1997
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 2000,
        color: 'black',
      },
      {
        textContent: 2004,
        color: 'white',
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-10',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-10',
          // markers: true,
        },
      }
    );

    // .render-10: Change h1 and p color
    gsap.fromTo(
      'div.render-10 *',
      {
        // textContent: 1986,
        color: 'black',
      },
      {
        // textContent: 1991,
        color: 'white',
        // snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-10',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-10',
          // markers: true,
        },
      }
    );
    // .render-11: *INCREMENT* THE YEAR:6: 1994 to 1997
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 2004,
        color: 'white',
      },
      {
        textContent: 2004,
        color: 'black',
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-11',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-11',
          // markers: true,
        },
      }
    );

    // .render-12: *INCREMENT* THE YEAR:6: 1994 to 1997
    gsap.fromTo(
      '.section3__child-left__1 > h1',
      {
        textContent: 2004,
        color: 'black',
      },
      {
        textContent: 2006,
        color: 'white',
        snap: { textContent: 1 }, //rounds to whole number
        scrollTrigger: {
          trigger: '.render-12',
          start: 'top 80%',
          end: 'bottom 80%',
          // end: '+=5000',
          id: 'render-12',
        },
      }
    );

    // .render-12: Change h1 and p color
    gsap.fromTo(
      'div.render-12 *',
      {
        color: 'black',
      },
      {
        color: 'white',
        scrollTrigger: {
          trigger: '.render-12',
          start: 'top 80%',
          end: 'bottom 80%',
          id: 'render-12',
        },
      }
    );

    // Keep this code below all year number increments
    // Set the year to 1984 when we first see it
    gsap.set('.section3__child-left__1 > h1', {
      textContent: 1984,
      color: 'white',
    });
  }
});
