export const body = document.querySelector('body');
export const checkbox = document.querySelector('#menuToggle > input');
export const overlay = document.querySelector('div.overlay');

// Left-side menu bar
export const handleCheckboxClick = (e) => {
  if (document.querySelector('#menu').contains(e.target)) {
    // Clicked inside the menu
  } else {
    // Clicked outside the menu
    checkbox.checked = false;
    body.classList.remove('fixed', 'overflow-hidden', 'h-full');
    window.removeEventListener('click', handleCheckboxClick);
    // hide dark overlay
    overlay.classList.remove('show-overlay');
  }
};

checkbox.addEventListener('change', function (e) {
  if (checkbox.checked) {
    // lock body scroll while left-side nav is open
    body.classList.remove('relative');
    body.classList.add('fixed', 'overflow-hidden', 'h-full');

    window.addEventListener('click', handleCheckboxClick);

    // show dark overlay
    overlay.classList.add('show-overlay');
  } else {
    body.classList.remove('fixed', 'overflow-hidden', 'h-full');
  }
});
