// Target '#section' to inject the HTML
let section = document.querySelector('#section');

// New HTML string
let newStr = '';

export function createSections() {
  //For each object, generate an HTML section
  contentArray.forEach((item, index) => {
    newStr += `<div class="${
      'render-' + (index + 1)
    } section scroll-snap full-w-h p2 placeholder text-white flex flex-col bg-black p-4 md:p-16 md:pb-28 md:flex-row md:justify-between md:gap-x-6" style="background-color:${
      item.bcgColor
    }; background-image:url(${item.bcgImg})">

  <div class="md:flex md:flex-row md:gap-x-10">
    
    <!-- FLEX CHILD 1 -->
    <div class="md:w-140">
    </div>

    <!-- FLEX CHILD 2 -->
    <div class="md:w-420 section__child-left__2 justify-between md:flex md:flex-col">

      <h1 class="mt-0 mb-8">
        ${item.headline}
      </h1>

      <p class="mb-16 md:mb-0">
      ${item.paragraph}
      </p>
    </div>

  </div>

  ${
    item &&
    item.imgsrc &&
    `<img src=${item.imgsrc} class="label section__child-right w-340 md:w-495" alt=" Placeholder" style="background-color:gray">`
  }

</div>`;
  });

  section.innerHTML = newStr;
}
